.LoadIndicator {
  padding-top: 200px;
  font-size: 1.3em;
  font-weight: bold;
  text-align: center;
  background-size: cover;
  background: #b6b6b6 fixed;
  height: 100vh; }

.LoadIndicator__Logo {
  -webkit-animation: LogoSpin infinite 20s linear;
          animation: LogoSpin infinite 20s linear;
  height: 40px;
  display: inline-block; }

@-webkit-keyframes LogoSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes LogoSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



